import React, { useState } from "react";
import { Button, Tag, ClickableTile } from "carbon-components-react";
// import { Button, Link, Tag, ClickableTile } from "carbon-components-react";
import { Time20, ArrowRight20 } from "@carbon/icons-react";
import "./ListPanelItem.scss";
// import { useEffect } from "react";

export const ListPanelItem = ({
  name,
  desc,
  tags,
  durationMins,
  handleLinkClick,
  handleDetailsActive,
  handleDetailsClick,
  hasButtons,
}) => {
  const baseClass = "list-panel-item";
  const [active, setActive] = useState({
    focus: false,
    pointer: false,
  });
  const [itemClasses, setItemClasses] = useState(baseClass);
  const handleClick = (preview) => {
    handleLinkClick(preview === true);
  };
  const updateItemClasses = (newActive) => {
    setItemClasses(
      newActive.focus || newActive.pointer
        ? `${baseClass} ${baseClass}--active`
        : `${baseClass}`
    );
  };
  const handleActivate = (e) => {
    const newState = { ...active };
    const type =
      e.type === "focus" ? e.type : e.type.substr(0, e.type.length - 5);
    newState[type] = true;
    setActive(newState);
    updateItemClasses(newState);
  };
  const handleDeactivate = (e) => {
    // console.log("Blur " + e.target.classList);
    const newState = { ...active };
    const type =
      e.type === "blur" ? "focus" : e.type.substr(0, e.type.length - 5);
    newState[type] = false;
    setActive(newState);
    updateItemClasses(newState);
  };
  return (
    <li
      className={itemClasses}
      onFocus={handleActivate}
      onBlur={handleDeactivate}
      onPointerEnter={handleActivate}
      onPointerLeave={handleDeactivate}
    >
      <ClickableTile light handleClick={handleClick} tabIndex="0">
        <div className="list-panel-item__heading">
          <h2 className="list-panel-item__title">{name}</h2>
        </div>
        <p className="list-panel-item__text text--long">
          {desc}
          {
            /*
            <Link
              onClick={(e) => {
                e.stopPropagation();
                handleClick(true);
              }}
              href="javascript:void(0)"
            >
              ...read more
            </Link>
            */
          }
        </p>
        <div className="list-panel-item__lower">
          <div className="list-panel-item__lower-left">
            {durationMins && (
              <React.Fragment>
                <Time20
                  className="list-panel-item__duration-img"
                  aria-label="Timing indicator"
                />
                <span className="list-panel-item__duration-text">
                  {durationMins} minutes
                </span>
              </React.Fragment>
            )}
            {tags && tags.map((tag) => <Tag key={tag}>{tag}</Tag>)}
          </div>
          <ArrowRight20
            className="list-panel-item__arrow-right"
            aria-label="Arrow indicating moving on"
          />
        </div>
      </ClickableTile>
      {hasButtons && (
        <div className="list-panel__buttons">
          <Button
            kind="primary"
            className="list-panel__button"
            onClick={handleClick}
          >
            Start
          </Button>
          <Button kind="ghost" className="list-panel__button">
            More details
          </Button>
        </div>
      )}
    </li>
  );
};
